import { CSSProperties } from "react"
import { Link } from "react-router-dom"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { ShareIcon } from "@rp/react/web/common/base-components"
import { useModal } from "@rp/react/web/common/providers"
import { useSnackbar } from "@rp/react/web/common/providers"
import { Box, IconButton, Typography } from "@rp/react/web/mui/core"
import { ArrowBack, Edit } from "@rp/react/web/mui/icons"

type Props = {
  id?: string
  name: string
  editable?: boolean
  sxContainer?: CSSProperties
  backNavigationUrl: string
  isPublic?: boolean
}

const WatchlistName = ({
  id,
  name,
  sxContainer,
  backNavigationUrl,
  editable = false,
  isPublic = false,
}: Props) => {
  const { enqueueSuccess } = useSnackbar()

  const {
    watchlistModal: { handleOpen },
  } = useModal()

  const onChangeName = async () => {
    handleOpen({ id, name }, () => {
      enqueueSuccess("Your watchlist has been saved successfully.")
    })
  }

  return (
    <Box
      sx={{
        display: "flex",
        alignItems: "center",
        gap: 1,
        maxWidth: "100%",
        flexGrow: 1,
        minWidth: 0,
        ...sxContainer,
      }}
    >
      <Link to={backNavigationUrl}>
        <IconButton size="small">
          <ArrowBack />
        </IconButton>
      </Link>
      <Box
        component="button"
        onClick={() => editable && onChangeName()}
        sx={{
          display: "flex",
          alignItems: "center",
          background: "transparent",
          border: 0,
          gap: 1,
          overflow: "hidden",
          cursor: editable ? "pointer" : undefined,
          "&:focus": {
            outline: 0,
          },
          "& > svg": {
            opacity: 0,
          },
          "&:hover > svg": {
            opacity: 1,
          },
        }}
      >
        <Box
          sx={{
            gap: 0.5,
            overflow: "hidden",
          }}
        >
          <Typography
            variant="h6"
            fontWeight={700}
            sx={{
              overflow: "hidden",
              textOverflow: "ellipsis",
              whiteSpace: "nowrap",
            }}
          >
            {name}
          </Typography>
        </Box>
        {isPublic ? (
          <Box sx={{ flexShrink: 0, display: "flex", alignItems: "bottom" }}>
            <ShareIcon shared />
          </Box>
        ) : null}
        {editable ? (
          <RPTooltip title="Rename" placement="right">
            <Edit sx={{ flexShrink: 0 }} />
          </RPTooltip>
        ) : null}
      </Box>
    </Box>
  )
}

export { WatchlistName }
